
import React, { useState, useEffect } from 'react';
import { Button, ButtonGroup, Container } from 'react-bootstrap';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import OverviewContent from './OverviewContent';
import { Separator, RadioDD, CheckboxDD, SearchCheckboxDD } from '../../components/customDropdown';
import { FilterContainer, FilterModal } from '../../components/filter';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionMeasure, optionRange, optionRegion, optionMetro, optionVertical, optionGroupExport, optionGroupMethodology, optionGAM, optionImport, optionExport } from '../../constants/filterOptions';
import { comments } from '../../constants/comments';
import { API_BASE_URL } from '../../constants/config';


const Overview = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [overviewData, setData] = useState(null);
  const [activeTab, setActiveTab] = useState('export');
  const [exportData, setExportData] = useState([]);
  const [importData, setImportData] = useState([]);
  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true);
  };
  const onCloseFilter = async () => {
    await onCancelRegion();
    await onCancelMetro();
    await onCancelVertical();
    await onCancelMeasure();
    await onCancelRange();
    await onCancelGroup1();
    setShowFilter(false);
  };
  const onApplyFilter = async () => {
    await onApplyRegion();
    await onApplyMetro();
    await onApplyVertical();
    await onApplyMeasure();
    await onApplyRange();
    await onApplyGroup1();
    setShowFilter(false);
  };

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  };
  const onShowComment = () => {
    setShowComment(true);
  };

  // checkbox for region values
  const [region_val, set_region_val] = useState([]);
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val);
  };
  const onCancelRegion = () => {
    set_region_val(region_val_onApply);
  };

  // searchCheckbox for metro values
  const [metro_val, set_metro_val] = useState([]);
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val);
  };
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply);
  };

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0] : null;
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val);
  };
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply);
  };

  // radio for range values
  const range_initial_value = optionRange ? optionRange[0] : null;
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val);
  };
  const onCancelRange = () => {
    set_range_val(range_val_onApply);
  };

  // radio for vertical values
  const vertical_initial_value = optionVertical ? optionVertical[0] : null;
  const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
  const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
  const onApplyVertical = () => {
    set_vertical_val_onApply(vertical_val);
  };
  const onCancelVertical = () => {
    set_vertical_val(vertical_val_onApply);
  };

  // radio for group2 values
  const group2_initial_value = optionGroupExport ? optionGroupExport[0] : null;
  const [group2_val_onApply, set_group2_val_onApply] = useState(group2_initial_value);
  const [group2_val, set_group2_val] = useState(group2_initial_value);
  const onApplyGroup1 = () => {
    set_group2_val_onApply(group2_val);
  };
  const onCancelGroup1 = () => {
    set_group2_val(group2_val_onApply);
  };

  // radio for methodology values
  const methodology_initial_value = optionGroupMethodology ? optionGroupMethodology[0] : null;
  const [methodology_val_onApply, set_methodology_val_onApply] = useState(methodology_initial_value);
  const [methodology_val, set_methodology_val] = useState(methodology_initial_value);
  const onApplyMethodology = () => {
    set_methodology_val_onApply(methodology_val);
  };
  const onCancelMethodology = () => {
    set_methodology_val(methodology_val_onApply);
  };

  // radio for import values
  const import_initial_value = optionImport ? optionImport[0] : null;
  const [import_val_onApply, set_import_val_onApply] = useState(import_initial_value);
  const [import_val, set_import_val] = useState(import_initial_value);
  const onApplyImport = () => {
    set_import_val_onApply(import_val);
  };
  const onCancelImport = () => {
    set_import_val(import_val_onApply);
  };

  // radio for export values
  const export_initial_value = optionExport ? optionExport[0] : null;
  const [export_val_onApply, set_export_val_onApply] = useState(export_initial_value);
  const [export_val, set_export_val] = useState(export_initial_value);
  const onApplyExport = () => {
    set_export_val_onApply(export_val);
  };
  const onCancelExport = () => {
    set_export_val(export_val_onApply);
  };


  // radio for gam values
  const gam_initial_value = optionGAM ? optionGAM[0] : null;
  const [gam_val_onApply, set_gam_val_onApply] = useState(gam_initial_value);
  const [gam_val, set_gam_val] = useState(gam_initial_value);
  const onApplyGam = () => {
    set_gam_val_onApply(gam_val);
  };
  const onCancelGam = () => {
    set_gam_val(gam_val_onApply);
  };
  // Axios call
  const fetchData = () => {
    let url = `${API_BASE_URL}/cross-border-trade/overview.json?print=pretty`;
    //Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // let url = `${API_BASE}/orbis/api/customerrevenueoverview`;
    // let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}&vertical=${vertical_val_onApply.key}&groupby=${group2_val_onApply.key}`;
    // let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';
    // let metroParameter = metro_val.length > 0 ? `&metro=${metro_val_onApply.join(",").toString()}` : '';

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url
      //Uncomment only when API is now available and working
      // url: url + defaultQueryParameters + regionParameter + metroParameter,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      // Uncomment only if for API testing purposes
      // console.log(result.config.url)
      setData(result.data);
      setImportData(result.data['import-region']);
      setExportData(result.data['export-region']);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, [
    region_val_onApply,
    metro_val_onApply,
    measure_val_onApply,
    range_val_onApply,
    vertical_val_onApply,
    group2_val_onApply
  ]
  );

  if (isLoading) return <Loader withContainer />;

  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
        {overviewData ?
          overviewData ?
            <>
              <FilterContainer
                showComment={showComment}
                onShowFilter={onShowFilter}
                onShowComment={onShowComment}
              >
                <div className="filters">
                  {/* <ButtonGroup className="sub-tab">
                    <Button
                      variant='light'
                      className={activeTab === 'export' ? 'active' : ''}
                      onClick={() => { setActiveTab('export') }}>
                      Export Region
                		</Button>
                    <Button
                      variant='light'
                      className={activeTab === 'import' ? 'active' : ''}
                      onClick={() => { setActiveTab('import') }}>
                      Import Region
                		</Button>

                  </ButtonGroup>
                  <Separator height='39px' /> */}
                  <RadioDD
                    title="Export Region"
                    id="export-region"
                    options={optionExport}
                    radioVal={export_val}
                    setRadioVal={set_export_val}
                    onApply={onApplyExport}
                    onCancel={onCancelExport}
                    initialValue={import_initial_value}
                  />
                  <RadioDD
                    title="Import Region"
                    id="import-region"
                    options={optionImport}
                    radioVal={import_val}
                    setRadioVal={set_import_val}
                    onApply={onApplyImport}
                    onCancel={onCancelImport}
                    initialValue={import_initial_value}
                  />
                  <RadioDD
                    title="Time Period"
                    id="range"
                    options={optionRange}
                    radioVal={range_val}
                    setRadioVal={set_range_val}
                    onApply={onApplyRange}
                    onCancel={onCancelRange}
                    initialValue={range_initial_value}
                  />
                  <RadioDD
                    title="Group by"
                    id="group"
                    options={optionGroupExport}
                    radioVal={group2_val}
                    setRadioVal={set_group2_val}
                    onApply={onApplyGroup1}
                    onCancel={onCancelGroup1}
                    initialValue={group2_initial_value}
                  />
                  <RadioDD
                    id="gam"
                    options={optionGAM}
                    radioVal={gam_val}
                    setRadioVal={set_gam_val}
                    onApply={onApplyGam}
                    onCancel={onCancelGam}
                  />
                  <RadioDD
                    title="Group by"
                    id="group-methodology"
                    options={optionGroupMethodology}
                    radioVal={methodology_val}
                    setRadioVal={set_methodology_val}
                    onApply={onApplyMethodology}
                    onCancel={onCancelMethodology}
                    initialValue={methodology_initial_value}
                  />
                </div>

                <FilterModal
                  title="Overview"
                  setShowFilter={setShowFilter}
                  showFilter={showFilter}
                  onCloseFilter={onCloseFilter}
                  onApplyFilter={onApplyFilter}
                >
                  {/* <CheckboxDD
                    title="Deploy Region"
                    id="region-modal"
                    options={optionRegion}
                    visibleInModal
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                  />
                  <SearchCheckboxDD
                    title="Deploy Metro"
                    id="metro-modal"
                    options={optionMetro}
                    visibleInModal
                    checkVal={metro_val}
                    setCheckVal={set_metro_val}
                  />
                  <RadioDD
                    title="Measure"
                    id="measure-modal"
                    options={optionMeasure}
                    visibleInModal
                    radioVal={measure_val}
                    setRadioVal={set_measure_val}
                  /> */}
                  <RadioDD
                    title="Export Region"
                    id="export-region"
                    visibleInModal
                    options={optionExport}
                    radioVal={export_val}
                    setRadioVal={set_export_val}
                    onApply={onApplyExport}
                    onCancel={onCancelExport}
                    initialValue={import_initial_value}
                  />
                  <RadioDD
                    title="Import Region"
                    id="import-region"
                    visibleInModal
                    options={optionImport}
                    radioVal={import_val}
                    setRadioVal={set_import_val}
                    onApply={onApplyImport}
                    onCancel={onCancelImport}
                    initialValue={import_initial_value}
                  />
                  <RadioDD
                    title="Time Period"
                    id="range-modal"
                    options={optionRange}
                    visibleInModal
                    radioVal={range_val}
                    setRadioVal={set_range_val}
                  />
                  <RadioDD
                    title="Vertical"
                    id="vertical-modal"
                    options={optionVertical}
                    visibleInModal
                    radioVal={vertical_val}
                    setRadioVal={set_vertical_val}
                  />
                  <RadioDD
                    title="Group by"
                    id="group-modal"
                    options={optionGroupExport}
                    visibleInModal
                    radioVal={group2_val}
                    setRadioVal={set_group2_val}
                  />
                  <RadioDD
                    id="gam"
                    options={optionGAM}
                    radioVal={gam_val}
                    visibleInModal
                    setRadioVal={set_gam_val}
                    onApply={onApplyGam}
                    onCancel={onCancelGam}
                  />
                  <RadioDD
                    title="Methodology"
                    id="group-methodology"
                    options={optionGroupMethodology}
                    radioVal={methodology_val}
                    setRadioVal={set_methodology_val}
                    onApply={onApplyMethodology}
                    onCancel={onCancelMethodology}
                    visibleInModal
                    initialValue={methodology_initial_value}
                  />
                </FilterModal>
              </FilterContainer>
              <OverviewContent
                overviewData={overviewData}
                selectedRangeValue={range_val_onApply.key}
              />
            </>
            : <ResourceNotFoundError redirectURL={"/executive-cockpit/gross-bookings-revenue/overview"} />
          : <InternalServerError redirectURL={"/executive-cockpit/gross-bookings-revenue/overview"} />}
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Overview;
