import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

import { convertToMnumber, convertToWholePercent } from '../../constants/formatter';

am4core.useTheme(am4themes_animated);


const StackedColumnChart = (props) => {
  const { id,
    largerWidth,
    data,
    hiddenValueAxis,
    legend,
    isPercentage,
    isOneHundredPercent,
    startValue,
    endValue,
    hideLabelBullet,
    percentLabel,
    insideLabel,
    isOverviewChart,
    isGrowthPercentToggable,
    stackedColumnSelection,
    selectedRangeValue,
    disablePaddingTop,
    categoryAxisLabelDisabled,
    isAnimationEnabled
  } = props;

  am4core.options.autoSetClassName = true;

  useLayoutEffect(() => {
    let chart = am4core.create(id, am4charts.XYChart);
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);

    chart.paddingRight = 0;
    chart.paddingLeft = 0;
    chart.paddingBottom = 0;
    if (disablePaddingTop) {
      chart.paddingTop = 0;
    } else {
      chart.paddingTop = 40;
    }

    chart.data = data;

    chart.responsive.enabled = true;

    let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "category";
    categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.renderer.grid.template.strokeWidth = 0;
    categoryAxis.renderer.minGridDistance = 50;
    categoryAxis.id = "myCategoryAxis";
    // categoryAxis.renderer.paddingTop = -8;
    // categoryAxis.renderer.paddingBottom = -8;

    if (categoryAxisLabelDisabled) {
      // categoryAxis.renderer.labels.template.disabled = true;
      categoryAxis.renderer.paddingTop = 0;
      categoryAxis.renderer.paddingBottom = 0;
      categoryAxis.renderer.marginTop = 0;
      categoryAxis.renderer.marginBottom = 0;
    }

    let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.renderer.grid.template.location = 0;
    valueAxis.renderer.minGridDistance = 50;
    valueAxis.id = "myValueAxis";
    // valueAxis.renderer.paddingLeft = -8;
    // valueAxis.renderer.paddingRight = -8;
    valueAxis.min = startValue;
    valueAxis.max = endValue;

    if (hiddenValueAxis === true) {
      valueAxis.renderer.grid.template.disabled = true;
      valueAxis.renderer.labels.template.disabled = true;
    } else {
      valueAxis.renderer.grid.template.strokeWidth = 0.5;
      valueAxis.renderer.labels.template.adapter.add("text", (label, target, key) => {
        return convertToMnumber(label);
      });
    }

    const createSeries = (field, name, color) => {
      let series = chart.series.push(new am4charts.ColumnSeries());
      let tooltipTextPercent = "[#fff]{name}: [#fff font-weight: bold]{valueY.formatNumber('#.0')}%[/][/]";
      let tooltipTextWithTotal = "[#fff]{name}: [#fff font-weight: bold] {valueY.formatNumber('#,###')}[/][/]\n[#fff]{category} Total: [#fff font-weight: bold] {valueY.total}[/][/]";

      if (largerWidth === true) {
        if (isOverviewChart === true) {
          series.columns.template.width = am4core.percent(50);
        } else {
          series.columns.template.width = am4core.percent(45);
        }
      } else {
        series.columns.template.width = am4core.percent(34);
      }

      series.columns.template.fill = am4core.color(color);
      series.columns.template.stroke = am4core.color(color);
      series.columns.template.tooltipText = isPercentage === true ? tooltipTextPercent : tooltipTextWithTotal;
      series.name = name;
      series.dataFields.categoryX = "category";
      series.dataFields.valueY = field;
      // series.dataItems.template.locations.categoryX = 0.5;
      series.stacked = true;
      series.tooltip.pointerOrientation = "vertical";
      series.hiddenState.transitionDuration = 0;
      series.defaultState.transitionDuration = isAnimationEnabled === true ? 1000 : 0;

      if (insideLabel) {
        var labelBullet = series.bullets.push(new am4charts.LabelBullet());

        if (percentLabel) {
          labelBullet.label.text = "{valueY.formatNumber('#.0')}%";
        } else {
          labelBullet.label.text = "{valueY.formatNumber('#.')}";
        }

        // labelBullet.locationY = 0.5;
        labelBullet.label.fill = am4core.color("#fff");
        labelBullet.label.hideOversized = true;
        labelBullet.label.numberFormatter.numberFormat = "#.";
      }

      return series;
    };

    let seriesArr = [];
    for (let i = 0; i < legend.length; i++) {
      seriesArr.push(createSeries(legend[i].value_name, legend[i].name, legend[i].color));
    }

    if (isOneHundredPercent === true) {
      valueAxis.min = 0;
      valueAxis.max = 100;
      valueAxis.strictMinMax = true;
    } else if (hideLabelBullet === false) {
      let bullet = seriesArr[seriesArr.length - 1].bullets.push(new am4charts.LabelBullet());

      bullet.label.text = "{valueY.total}";
      bullet.label.fill = am4core.color("#748A9D");
      bullet.label.verticalCenter = "bottom";
      bullet.label.textAlign = "middle"
      bullet.label.adapter.add("text", (label, target, key) => {
        let total = target.dataItem.values.valueY.total;

        if (isGrowthPercentToggable === true && selectedRangeValue === "last_completed_quarter") {
          let growthPercent = target.dataItem.dataContext[stackedColumnSelection];
          let color = growthPercent > 0.5 ? '#00b050' : growthPercent < -0.5 ? '#ff3333' : '#f0cc71';
          let labelWithGrowthPercent = `[font-size: 9.5px]${convertToMnumber(total)}\n[font-size: 11px; ${color}](${convertToWholePercent(growthPercent)}%)[/][/]\n[font-size: 5px; #fff]space[/]`;
          let labelWithNoGrowthPercent = `[font-size: 9.5px]${convertToMnumber(total)}[/]\n[font-size: 5px; #fff]space[/]`;
          return isOverviewChart === true && (growthPercent !== '' || growthPercent !== null || growthPercent !== 0) ? labelWithGrowthPercent : labelWithNoGrowthPercent;
        } else {
          let defaultLabel = `[font-size: 9.5px]${convertToMnumber(total)}[/]\n[font-size: 5px; #fff]space[/]`;
          return isOverviewChart === true ? defaultLabel : '';
        }
      });

      valueAxis.calculateTotals = true;
    }

    chart.maskBullets = false;

    return () => {
      chart.dispose();
    };
  }, [id, largerWidth, data, hiddenValueAxis, legend, isPercentage, isOneHundredPercent, startValue, endValue, hideLabelBullet, insideLabel, isOverviewChart, percentLabel, isGrowthPercentToggable, stackedColumnSelection, selectedRangeValue, disablePaddingTop, categoryAxisLabelDisabled, isAnimationEnabled]
  );

  return (
    <div className="stacked-column-chart" id={id}></div>
  );
}

export default StackedColumnChart;