const AddMetricsModal = (props) => {
  const { showBuilderModal, onCancelModal, selectedItem, setSelectedItem, setNextPage, stepList, setStepList } = props;
  const [checkedList, setCheckedList] = useState(stepList);
  const [filteredList, setFilteredList] = useState([]);
  const product_initial_value = productOption ? productOption[0] : null;
  const [productSelection, setProductSelection] = useState(product_initial_value);

console.log('=======add metrics modal======')
console.log(selectedItem)

  const onCancel = async () => {
    await onCancelModal()
  }

  const checkButton = (item) => {

    // let newList = checkedList;
    var newArray = selectedItem.slice();
    var finalArray = [];
    var arr = stepList.map((val) => {
      if (val.isChecked === true && val.key === item.key) {
        val.isChecked = false;
        finalArray = selectedItem.filter(data => {
          return data.key != item.key
        })
        setSelectedItem(finalArray)
      } else if (val.key === item.key) {
        val.isChecked = true;
        newArray.push(item);
        setSelectedItem(newArray)

      }
      return val
    })
    console.log('selectedItem', selectedItem)

    setCheckedList(arr);
    console.log(arr);





    // newList.map((index) => {
    //   // check if already true then set value to false
    //   if (index.isChecked === true && index.key === item.key) {
    //     index.isChecked = false;
    //     setCheckedList([...checkedList]);
    //   } else {
    //     // check if id is equal
    //     if (index.key === item.key) {
    //       index.isChecked = true;
    //       setCheckedList([...checkedList]);
    //     }
    //   }

    // })
  }



  //arrange alphabetically




  return (
    <>
      <Modal
        show={showBuilderModal}
        onHide={onCancel}
        className="product-modal"
        backdrop="static"
        keyboard={false}
        centered
      >
        <div className="to-prevent-glitch">prevent glitch</div>
        <div className="content-wrapper">

          <AnimatePresence exitBeforeEnter>
            <ContentContainer
              modalTitle='CHOOSE ADD METRICS TO ADD'
            >
              <div className="filters custom-scroll">
                <BasicDD
                  title="All Categories"
                  id="all_categories"
                  addTitleOnWide
                  value={productSelection}
                  setValue={setProductSelection}
                  options={productOption}
                  onClick={() => {
                    // if (productSelection) {
                    //   const substring = productSelection.key;

                    //   checkedList.map(item => {
                    //     if (item.key.includes(substring)) {
                    //       filteredList.push(item)
                    //     }
                    //   })
                    // }
                  }}
                />
              </div>
              <Modal.Body>
                <Form>
                  <div className="product-container">

                    {checkedList && checkedList.sort(function (a, b) {
                      if (a.text < b.text) { return -1; }
                      if (a.text > b.text) { return 1; }
                      return 0;
                    }).map((item) => {
                      return (
                        <div className="btn-container" key={item.key}>
                          <Button
                            variant="link"
                            id={item.key}
                            className={`checkbox-${item.isChecked}`}
                            onClick={() => checkButton(item)}
                          >
                            {item.icon}{item.text}
                          </Button>
                        </div>
                      )
                    })}

                  </div>

                </Form>

              </Modal.Body>

              <Modal.Footer>
                <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                {/* <Button variant="primary" type="submit" onClick={() => goTo(selectedItem)} > Next</Button> */}
                <Button variant="primary" type="submit" onClick={() => setNextPage(true)} > Next</Button>
              </Modal.Footer>
            </ContentContainer>
          </AnimatePresence>
        </div>
      </Modal>
    </>
  )
};

const ContentContainer = ({
  modalTitle,
  children, width
}) => {
  return (
    <motion.div className={`content-container`}
      animate='visible'
    >
      {modalTitle &&
        <Modal.Header>
          <Modal.Title>{modalTitle}</Modal.Title>
        </Modal.Header>
      }
      {children}
    </motion.div>
  )
}


export default AddMetricsModal;